@import "../../../styles/vars";
@import "../../../styles/mixins";

.aboutProduct {
  &__container {
    text-align: center;
    @include setDistance(margin-bottom, 60)
  }

  &__contentText {
    /*max-width: 370px;*/
    max-width: 485px;
    display: inline-block;
    @include setDistance(margin-bottom, 60);
    margin-top: 33px;

    @include smScreenMedia {
      max-width: 260px;
      margin-top: 20px;
   }
}

  &__slide {
    display: flex !important;
    flex-direction: column;
    gap: 40px;
    padding: 0 10px;

    @include lgScreenMedia {
      gap: 20px;
    }

    @include mdScreenMedia {
      margin-bottom: 40px;
    }

    @include smScreenMedia {
      margin-bottom: 20px;
    }
  }

&__imgSlide {
    width: 100%;
  }
}